.app-main {
	background-image: radial-gradient(circle at 20% 100%,
	rgba(184, 184, 184, 0.1) 0%,
	rgba(184, 184, 184, 0.1) 33%,
	rgba(96, 96, 96, 0.1) 33%,
	rgba(96, 96, 96, 0.1) 66%,
	rgba(7, 7, 7, 0.1) 66%,
	rgba(7, 7, 7, 0.1) 99%),
  linear-gradient(40deg, #040a22, #020924, #040d2e, #000a30);
}

/*drawer styles*/
.mobile-nav-container {
	background-image: radial-gradient(circle at 20% 100%,
			rgba(184, 184, 184, 0.1) 0%,
			rgba(184, 184, 184, 0.1) 33%,
			rgba(96, 96, 96, 0.1) 33%,
			rgba(96, 96, 96, 0.1) 66%,
			rgba(7, 7, 7, 0.1) 66%,
			rgba(7, 7, 7, 0.1) 99%),
		linear-gradient(40deg, #040a2288, #0c14358e, #030b2998, #0e2686);
	background-repeat: no-repeat;
	background-size: cover;
}

.mobile-nav-elements {
	height: 100vh;
	overflow: hidden;
}

.header-drawer {
	background-color: rgba(34, 192, 195, 0);
}

.toolbar {
	background-image: radial-gradient(0% 100%,
			rgba(184, 184, 184, 0.1) 0%,
			rgba(184, 184, 184, 0.1) 33%,
			rgba(96, 96, 96, 0.1) 33%,
			rgba(96, 96, 96, 0.1) 66%,
			rgba(7, 7, 7, 0.1) 66%,
			rgba(7, 7, 7, 0.1) 99%),
		linear-gradient(40deg, #040a22, #3b425f, #000a30, #040a22);
	background-repeat: no-repeat;
	background-size: cover;
	box-shadow: 2px 9px 5px 0px rgba(0, 0, 0, 0.66);
	-webkit-box-shadow: 2px 9px 5px 0px rgba(0, 0, 0, 0.66);
	-moz-box-shadow: 2px 9px 5px 0px rgba(0, 0, 0, 0.66);
}

.mobile-nav-elements ul {
	height: 60%;
}

.mobile-nav-elements ul :hover {
	color: rgb(255, 255, 255);
	transition: 0.3s ease-in-out;
	text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #fff;
}

.mobile-nav-elements ul :active {
	color: rgb(255, 255, 255);
	transition: 0.3s ease-in-out;
	text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #fff;
}

.nav-img {
position: absolute;
left: 150px;
	height: 190px;
	width: 250px;
}

@media (max-width: 500px) {
	.nav-img {
		position: absolute;
		left: 100px;
		height: 180px;
		width: 200px;
		margin: 0 auto;
	}
}







/*3card test */

.gr-1 {
  background: linear-gradient(170deg, #01484e 0%, #010f58 100%);
}

.gr-2 {
  background: linear-gradient(170deg, #4a7403 0%, #143d04 100%);
}

.gr-3 {
  background: linear-gradient(170deg, #3d0846 0%, #070330 100%);
}

.column {
  margin-top: 3rem;
  padding-left: 3rem;
}
.column:hover {
  padding-left: 0;
  transition: 0.5s;
}
.column:hover .treecard .txt {
  margin-left: 1rem;
  transition: 0.5s;
}
.column:hover .treecard .txt h1,
.column:hover .treecard .txt p {
  color: white;
  opacity: 1;
  transition: 0.5s;

}
.column:hover a {
  color: white;
  transition: 0.5s;

}
.column:hover a:after {
  width: 10%;
}

.treecard {
  min-height: 120px;
  margin: 0;
  padding: 1.7rem 1.2rem;
  border: none;
  border-radius: 0;
  color: rgb(255, 255, 255);
  letter-spacing: 0.05rem;
  border-radius: 1rem;
  font-family: "Oswald", sans-serif;
  box-shadow: 0 0 21px rgba(0, 0, 0, 0.27);
}
.treecard .txt {
  margin-left: -3rem;
  z-index: 1;
}
.treecard .txt h1 {
  font-size: 1.9rem;
  font-weight: 300;
}
.treecard .txt p {
  font-size: 0.7rem;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 0rem;
  margin-top: 33px;
  opacity: 0;
  color: white;
  transition: 0.5s;
}
.treecard a {
  z-index: 3;
  font-size: 0.7rem;
  color: rgb(255, 255, 255);
  margin-left: 1rem;
  position: relative;
  bottom: -0.5rem;
  text-transform: uppercase;
}
.treecard a:after {
  content: "";
  display: inline-block;
  height: 0.5em;
  width: 0;
  margin-right: -100%;
  margin-left: 10px;
  border-top: 1px solid white;
  transition: 0.5s;
}
.treecard .ico-card {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 100;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.treecard i {
  position: relative;
  right: -50%;
  top:40%;
  font-size: 12rem;
  line-height: 0;
  opacity: 0.2;
  color: white;
  z-index: 0;
}