
/*card package styles*/

.cardcontainer .box {
    position: relative;
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 50px;
    transition: 0.3s;
  }
  
  .cardcontainer .box::before {
    content: ' ';
    position: absolute;
    top: 0;
    left: 50px;
    width: 50%;
    height: 100%;
    text-decoration: none;
    background: #fff;
    border-radius: 8px;
    transform: skewX(15deg);
    transition: 0.5s;
  }
  
  .cardcontainer .box::after {
    content: '';
    position: absolute;
    top: 0;
    left: 50;
    width: 50%;
    height: 100%;
    background: #fff;
    border-radius: 8px;
    transform: skewX(15deg);
    transition: 0.5s;
    filter: blur(30px);
  }
  
  .cardcontainer .box:hover:before,
  .cardcontainer .box:hover:after {
    transform: skewX(0deg);
    left: 20px;
    width: calc(100% - 90px);
  
  }
  
  .cardcontainer .box:nth-child(1):before,
  .cardcontainer .box:nth-child(1):after {
    background: linear-gradient(315deg, #0a324b00, #9e75058a)
  }
  
  .cardcontainer .box:nth-child(2):before,
  .cardcontainer .box:nth-child(2):after {
    background: linear-gradient(315deg, #0a324b00, rgba(192, 192, 192, 0.795))
  }
  
  .cardcontainer .box:nth-child(3):before,
  .cardcontainer .box:nth-child(3):after {
    background: linear-gradient(315deg, #0a324b00, #92420169)
  }
  
  .cardcontainer .box:nth-child(4):before,
  .cardcontainer .box:nth-child(4):after {
    background: linear-gradient(315deg, #0a324b00, #0314ff69)
  }
  
  .cardcontainer .box:nth-child(5):before,
  .cardcontainer .box:nth-child(5):after {
    background: linear-gradient(315deg, #0a324b00, #92010169)
  }
  
  .cardcontainer .box:nth-child(6):before,
  .cardcontainer .box:nth-child(6):after {
    background: linear-gradient(315deg, #0a324b00, #00000069)
  }
  
  
  .cardcontainer .box span {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
    pointer-events: none;
  }
  
  .cardcontainer .box span::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    opacity: 0;
    transition: 0.1s;
    animation: animate 2s ease-in-out infinite;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08)
  }
  
  .cardcontainer .box:hover span::before {
    top: -50px;
    left: 50px;
    width: 100px;
    height: 100px;
    opacity: 1;
  }
  
  .cardcontainer .box span::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    opacity: 0;
    transition: 0.5s;
    animation: animate 2s ease-in-out infinite;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
    animation-delay: -1s;
  }
  
  .cardcontainer .box:hover span:after {
    bottom: -50px;
    right: 50px;
    width: 100px;
    height: 100px;
    opacity: 1;
  }
  
  @keyframes animate {
  
    0%,
    100% {
      transform: translateY(10px);
    }
  
    50% {
      transform: translate(-10px);
    }
  }
  
  .cardcontainer .box .content {
    position: relative;
    left: 0;
    padding: 20px 40px;
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(10px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    z-index: 1;
    transform: 0.5s;
    color: #fff;
  }
  
  .cardcontainer .box:hover .content {
    left: -25px;
    padding: 60px 40px;
  }
  
  .cardcardcontainer .box .content h2 {
    font-size: 2em;
    color: #fff;
    margin-bottom: 10px;
  }
  
  .cardcontainer .box .content p {
    font-size: 1.1em;
    margin-bottom: 10px;
    line-height: 1.4em;
  }
  
  .cardcontainer .box .content a {
    display: inline-block;
    font-size: 1.1em;
    color: #111;
    background: #fff;
    padding: 10px;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 700;
    margin-top: 5px;
  }
  
  .cardcontainer .box .content a:hover {
    background: #ffcf4d;
    border: 1px solid rgba(255, 0, 88, 0.4);
    box-shadow: 0 1px 15px rgba(1, 1, 1, 0.2);
  }
  
  .cardcontainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 60px 0;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    background-image: radial-gradient(circle at 50% 100%,
    rgba(184, 184, 184, 0.1) 0%,
    rgba(184, 184, 184, 0.1) 33%,
    rgba(96, 96, 96, 0.1) 33%,
    rgba(96, 96, 96, 0.1) 66%,
    rgba(7, 7, 7, 0.1) 66%,
    rgba(7, 7, 7, 0.1) 99%),
  linear-gradient(40deg, #030B2C, #030B2C, #0935af3d, #030B2C);
  background-repeat: no-repeat;
  background-size: cover;
  
    }
  